#navbar {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;    
    padding-left: 3%;
    padding-right: 3%;
    height: auto;
    margin: 0;
}

.navItem {
    color: #FC6E6A;
    padding: 3vh;
    margin-left: 2vh;
    font-size: 3vh;
    font-weight: bold;
    text-decoration: none;
    margin-top: 2%;
}

.navItem:hover {
    color: #FC6E6A;
    cursor: pointer;
    text-decoration: underline;
}

img {
    width: 30%;
    height:auto;
    margin-right: auto;
}